import Vue from 'vue'
import Router from 'vue-router'
import HomeIndex from '@/components/home-page/Index'
import ProfileIndex from '@/components/profile-page/Index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: HomeIndex
    },
    {
      path: '/profile/:username',
      name: 'Profile',
      component: ProfileIndex
    }
  ]
})



