<template>
  <!-- SignUp Start -->
  <section id="signup" class="form-section-4 width-100 float-left bg-bluish-white text-align-center padding-tb-84-70">
    <div class="container wow fadeInUp" data-wow-delay="0.5s">
      <!--Onde formos a montar esse componente, teremos de enviar o texto para o slot-->
      <slot></slot>
      <div class="row">
        <div class="col-lg-12 col-md-12 col-sm-12 col-xs-12">
          <div class="form-container form-style-5 text-align-left">
            <form method="POST" @submit.prevent="onSubmit" class="form-for-search row">
              <div class="col-lg-6 col-md-6 col-sm-4 col-xs-12">
                <div class="md-input md-text margin-bottom-30">
                  <input class="border-radius-25" name="website-url" id="website-url" type="text" placeholder="Seu site (opcional)">
                </div>
              </div>
              <div class="col-lg-3 col-md-3 col-sm-4 col-xs-12">
                <div class="md-input md-text margin-bottom-30">
                  <input class="border-radius-25" name="client-email" id="client-email" type="email" placeholder="Seu email">
                </div>
              </div>
              <div class="col-lg-3 col-md-3 col-sm-4 col-xs-12">
                <div class="md-form-submit width-100">
                  <input class="btn bg-light-brilliant-vermilion linear-transition md-form-active" value="Subscrever" type="submit" id="dt-form4-btn-1">
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </section>
  <!-- SignUp End -->
</template>

<script>
export default {
  data () {
    return {
    }
  },
  methods: {
    onSubmit () {
      alert('Ainda estamos em implementacao')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



