<template>
  <!-- SignUp Start -->
  <section id="signup" class="form-section-4 width-100 float-left bg-bluish-white text-align-center padding-tb-84-70">
    <div class="container wow fadeInUp" data-wow-delay="0.5s">
      <template>
        <h2 class="text-medium-38 font-weight-600 text-transform-capitalize color-black word-wrap margin-bottom-39">Anote aqui a sua <span class="color-light-brilliant-vermilion">idea</span></h2>
      </template>      <div class="row">
        <div class="col-md-8 col-md-offset-2 col-sm-12 col-xs-12">
          <div class="form-container form-style-5 text-align-left">
            <form method="POST" @submit.prevent="onSubmit" class="form-for-search row">
              <div class="col-xs-12">
                <div class="md-text margin-bottom-30">
                  <input class="border-radius-25" name="title" id="website-url" type="text" placeholder="Titulo da sua ideia">
                </div>
              </div>
              <div class="col-xs-12">
                <div class="md-text margin-bottom-30">
                  <input class="border-radius-25" name="description" id="client-email" type="text" placeholder="Descricao or fale da ideia">
                </div>
              </div>
              <div class="col-xs-12 pull-left">
                <div class="btn-group">
                  <button type="button" class="btn btn-default dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                    Privada
                    <span class="caret"></span>
                  </button>
                  <ul class="dropdown-menu">
                    <li><a href="#">Publica</a></li>
                  </ul>
                </div>
              </div>
              <div class=" col-md-4 col-xs-12 pull-right">
                <div class="md-form-submit width-100">
                  <input class="btn bg-light-brilliant-vermilion linear-transition md-form-active" value="Registar" type="submit" id="dt-form4-btn-1">
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </section>
  <!-- SignUp End -->
</template>

<script>
export default {
  data () {
    return {
    }
  },
  methods: {
    onSubmit () {
      alert('Ainda estamos em implementacao')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



