<template>
  <!-- Header start -->
  <header id="md-header" class="md-site-header md-header-style-4 cool-gray-and-dark-orange-menu width-100 float-left z-index-6 bg-white border-dim-gray-bottom">
    <div class="md-logo-navbar width-100 float-left linear-transition">
      <div class="container">
        <div class="row">
          <div class="col-lg-3 col-md-3 col-sm-3 col-xs-4">
            <div class="md-theme-logo">
              <!--./assets/logo.png-->
              <a href="#" class="" title="Home | Boa Ideia "><b style="font-size: 30pt">b</b><span style="font-size: 32pt;color: orange">D</span></a>
            </div>
          </div>

          <div class="col-lg-9 col-md-9 col-sm-9 col-xs-12 md-main-menu">
            <div class="menuBar jumplinks">
              <nav class="navbar navbar-default bg-transparent">
                <div class="navbar-header">
                  <button type="button" class="navbar-toggle collapsed border-none" data-toggle="collapse" data-target="#md-site-navbar" aria-expanded="false">
                    <span class="sr-only">Toggle navigation</span>
                    <span class="icon-bar"></span>
                    <span class="icon-bar"></span>
                    <span class="icon-bar"></span>
                  </button>
                </div>

                <div id="md-site-navbar" class="collapse navbar-collapse">
                  <ul class="nav navbar-nav navbar-right">
                    <!--Enviar os links aqui no slot-->
                    <slot></slot>
                    <li>
                      <!--Componente dropdown, com a foto do User (Veja no Shared/DropDown.vue)-->
                      <drop-down></drop-down>
                    </li>
                  </ul>
                </div>
              </nav>
            </div>
          </div>
        </div>
      </div>
    </div>
  </header>
  <!-- Header end -->
</template>

<script>
import DropDown from '../shared/DropDown.vue'

export default {
  components: {
    DropDown
  },
  data () {
    return {
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.header-position-fixed .menuBar {
  padding: 15px 0px;
}
</style>



